<template>
    <Navbar></Navbar>
    <router-view></router-view>
    <footerview></footerview>
</template>
<style>
.card-mt{
    margin-top: 10px;
}
.card-mt:last-child{
    margin-bottom: 10px;
}
.card-boxtitle{
    cursor: pointer;
}
.card-boxtitle p{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 30px;
    opacity: 0.8;
    transition: opacity 1s;
}
.navfixed{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}
.navfixed_opacity1{
    opacity: 1;
    transition: all 1s;
}
.navfixed_opacity0{
    opacity: 0;
    transition: all 1s;
}
/* .card-boxtitle:hover p{
    opacity: 1;
} */
</style>
<script>
import Navbar from '../components/Navbar.vue';
import footerview from '../components/FooterView.vue';
export default {
    name: 'Dashboard',
    data () {
        return {
            oldscroll: 0,
            headerbox: 0
        };
    },
    components: {
        Navbar,
        footerview
    },
    created () {
        window.addEventListener('scroll', this.scrollnavbar);
    },
    methods: {
        gethight () {
            const carousel = document.getElementById('carouselExampleIndicators').offsetHeight;
            const footer = document.getElementById('footerid').offsetHeight;
            const header = document.getElementById('navbarid').offsetHeight;
            const cardbox = document.getElementById('cardbox');
            const heighttotall = carousel + footer + header;
            this.headerbox = header;
            cardbox.style.cssText = `min-height:calc(100vh - ${heighttotall}px)`;
        },
        scrollnavbar () {
            const newscroll = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            const headerbox = document.getElementsByClassName('navbarbox');
            const scrollSet = newscroll - this.oldscroll;
            this.headerbox = document.getElementById('navbarid').offsetHeight;
            this.oldscroll = newscroll;
            headerbox[0].style.cssText = `min-height:${this.headerbox}px`;
            if (scrollSet > 0) {
                    document.getElementById('navbarid').classList.add('navfixed');
                    document.getElementById('navbarid').classList.add('navfixed_opacity1');
                    document.getElementById('navbarid').classList.remove('navfixed_opacity0');
            } else {
                    document.getElementById('navbarid').classList.add('navfixed_opacity0');
                if (newscroll <= 100) {
                    document.getElementById('navbarid').classList.remove('navfixed');
                    document.getElementById('navbarid').classList.remove('navfixed_opacity1');
                    document.getElementById('navbarid').classList.remove('navfixed_opacity0');
                }
            }
        }
    },
    unmounted () {
        window.removeEventListener('scroll', this.setNavPosition);
    }
};
</script>
